<template>
  <div class="register">
    <div class="app-logo">
      <div class="logo">
        <van-uploader class="logo-v" v-model="uploader.fileList" default round :max-count="1" />
      </div>
    </div>
    <div class="register-input">
      <van-cell-group>
        <van-field
          v-model="user.value"
          left-icon="manager"
          type="text"
          label="用户名"
          placeholder="请输入用户名"
          :error-message="user.errorMessage"
          @input="validUsername(user.value)"
        />
        <van-field
          v-model="pwd1.value"
          :type="pwd1.type"
          left-icon="lock"
          :right-icon="pwd1.icon"
          label="密码"
          placeholder="请输入密码"
          :error-message="pwd1.errorMessage"
          @click-right-icon="checkPassword('pwd1')"
          @input="vaildPassword(pwd1.value)"
        />
        <van-field
          v-model="pwd2.value"
          :type="pwd2.type"
          left-icon="lock"
          :right-icon="pwd2.icon"
          label="确认密码"
          placeholder="请输入确认密码"
          :error-message="pwd2.errorMessage"
          @click-right-icon="checkPassword('pwd2')"
          @input="vaildPwd(pwd1.value, pwd2.value)"
        />
      </van-cell-group>
    </div>
    <div class="register-form">
      <van-button
        class="register-btn"
        :loading="isLoading"
        type="primary"
        block
        loading-text="注册中..."
        text="注册"
        @click="register"
        :disabled="!isValid"
      />
    </div>
    <div class="loginTo">
      <router-link class="login-t" to="/login">已有账户？登录</router-link>
    </div>
  </div>
</template>

<script>
import { validForm } from "../tool/validForm";

import { createNamespacedHelpers } from "vuex";

// 基于registerModule的命名空间导入mapState、mapMutations 铺助函数

const { mapState, mapMutations } = createNamespacedHelpers("registerModule");

export default {
  name: "register",

  // 计算属性
  computed: {
    ...mapState(["pwd1", "pwd2", "user", "isLoading", "isValid", "uploader"])
  },

  // 方法
  methods: {
    ...mapMutations(["checkPassword"]),

    register() {
      let self = this;

      //注册
      this.$store.commit("registerModule/register", true);

      let randomT = Math.random() * 5 * 1000;

      setTimeout(function() {
        //获取所有用户信息
        let users = localStorage.getItem("users");

        users = users ? JSON.parse(users) : [];

        // console.log("users ==>> ", users);

        // 判断当前用户是否存在
        for (let i = 0; i < users.length; i++) {
          if (users[i].username == self.user.value) {
            self.$toast({
              duration: 2000,
              message: "该用户已存在"
            });
            sefl.$store.commit("registerModule/register", false);
            return;
          }
        }
        self.$toast({
          duration: 2000,
          message: "注册成功"
        });

        self.$store.commit("registerModule/register", false);

        let time = new Date().getTime();
        // console.log(self);
        // 记录用户信息
        let userInformation = {
          // 图片
          pho: self.uploader.fileList,

          // 用户名
          username: self.user.value,

          // 密码
          pwd: self.pwd1.value,

          // 注册时间
          time
        };

        // 将当前用户信息推荐users
        users.push(userInformation);

        // 写入本地存储， 模拟数据库
        localStorage.setItem("users", JSON.stringify(users));

        
        //跳转到登录页面
        self.$router.push({ name: "login" });
      }, randomT);

    },

    // 输入表单控件 用户
    validUsername(value) {
      // console.log("valueU ==>> ", value);

      let isValid = validForm.validUsername(value);

      this.$store.commit("registerModule/validUsername", isValid);
      this.$store.commit("registerModule/passForm");
    },

    //验证密码
    vaildPassword(value) {
      // console.log("valueP ==>> ", value);
      let isValid = validForm.vaildPassword(value);
      this.$store.commit("registerModule/vaildPassword", isValid);
      this.$store.commit("registerModule/passForm");
    },

    //验证确认密码
    vaildPwd(v1, v2) {
      let isValid = validForm.vaildPwd(v1, v2);

      this.$store.commit("registerModule/vaildPwd", isValid);

      this.$store.commit("registerModule/passForm");
    },

    //验证用户名或者密码
    validUP(value, fnName, o) {
      let isValid = validForm[fnName](value);

      o.isValid = isValid;

      this.$store.commit("registerModule/validUP", o);

      this.$store.commit("registerModule/passForm");
    }
  }
};
</script>
